.results-page {
    padding: 3rem 1rem;
}

.card {
    width: 345px;
}

.card-container {
    display: flex;
    justify-content: center;
    padding: 1rem;
}

.card-media {
    height: 140px;
}

.page-message {
    text-align: center;
}
.details-page {
    padding: 3rem 1rem;
    display: flex;
    justify-content: center;
}

.paper-container {
    padding: 1rem;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
}

.content {
    padding: 1rem 0rem;
}

.item-image {
    width: 100%;
    height: 250px;
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center;
}

.item-loader {
    padding: 1rem;
}
.card-media {
    height: 140px;
}
.main-container {
    position: relative;
    border-radius: 10px;
    background-color: rgba(255, 255, 255, 0.25);
    margin-right: 1rem;
    margin-left: 1rem;
    padding: 0 rem 1rem;
    justify-items: center;
    align-items: flex-end;
    display: flex;
    box-sizing: content-box;
    width: 400px;
}

.container-icon {
    height: 100%;
    pointer-events: none;
    margin-left: .5rem;
    margin-right: .5rem;
}

.container-results {
    position: absolute;
    z-index: 100000;
    margin-top: 2.5rem;
    left: 0;
    right: 0;
    top: 0;
    width: 90%;
    margin-left: auto;
    margin-right: auto;
}

.appbar {
    display: flex;
    justify-content: space-between;
}
.App-header {
    background-color: red;
    min-height: 150px;
    align-items: center;
    color: white;
